import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: resolve => require(['@/pages/home'], resolve),
      redirect: '/video',

      children:[
        {
          path: '/video',
          component: resolve => require(['@/components/video/video'], resolve)
        },

        {
          path: '/announce',
          component: resolve => require(['@/components/announce/announce'], resolve)
        }
      ]
    },

    {
      path: '/videoDetail',
      name: '视频详情页',
      component: resolve => require(['@/pages/video/detail'], resolve)
    },

    {
      path: '/announceDetail',
      name: '公告详情页',
      component: resolve => require(['@/pages/announce/detail'], resolve)
    }
  ]
})



